import * as yup from 'yup'

export const schemaYup = yup.object().shape({
  name: yup.string().trim()
    .required('La Institucin es obligatoria'),
  acronimo: yup.string().trim()
    .required('El Acrnimo es obligatorio'),
  servicio: yup.number()
    .required('El Servicio es obligatorio'),
  phonenumber: yup.string()
    .required('El Telfono es obligatorio')
    .length(10, 'Debe tener exactamente 10 dgitos'),
  address: yup.string().trim()
    .required('La Direccin es obligatoria'),
  email: yup.string().trim()
    .required('El Correo Electrnico es obligatorio')
    .email('El Correo Electrnico no es vlido'),
  website: yup.string().trim()
    .required('El Sitio Web es obligatorio'),
  encargado: yup.number()
    .required('El Encargado es obligatorio'),
})